@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  background-color: #bcaaa4;
  color: #3e2723;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 24px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
    text-decoration: none;
    margin-left: 3.6px;
    margin-right: 3.6px;
}

img {
    max-width: 100%;
    max-height: 500px;
    overflow: auto;
    border-radius: 8px;
}

h4 {
    font-weight: 700;
    text-shadow: 0 1px 0 black;
}

.header-bar {
    display: flex;
    background-color: #bcaaa4;
}

.links {
    position: absolute;
    right: 0;
}

img {
    max-width: 100%;
    max-height: 500px;
    overflow: auto;
    border-radius: 8px;
}

.info-container {
    display: flex;
    position: relative;
    flex-wrap: wrap;
}

.img-size {
    width: 100%;
    height: auto;
}

.img-wrapper {
    right: 0;
    margin-bottom:24px;
}

.resume-container {
    position: relative;
}

.text-wrapper {
    position: absolute;
    left: 0;
    margin: 24px;
    padding: 24px;
    z-index: 10;
    border-radius: 8px;
    background-color: #efebe9;
    width: 350px;
    height: auto;
    box-shadow: 5px 5px #d7ccc8;
}

.icon-container {
    padding: 24px 128px;
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 500px;
}

.center-container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.project-container {
    background-color: #efebe9;
    box-shadow: 5px 5px #d7ccc8;
    border-radius: 8px;
    padding: 24px;
    margin-bottom: 24px;
}

.bullet-wrapper {
    flex: 1 1;
    left: 0;
}

.project-img-wrapper {
    right: 0;
    margin-left: 24px;
}

.tabguard-size {
    width: 200px;
    height: 250px;
}

.predictor-size {
    width: 450px;
    height: 200px;
}

.c4me-size {
    width: 450px;
    height: 200px;
}

iframe.border {
    border-radius: 8px;
    overflow: hidden;
    min-width: 200px;
    min-height: 280px;
}

.interests-container {
    position: relative;
    display: flex;
    margin-bottom: 24px;

}

.interests-wrapper {
    background-color: #efebe9;
    box-shadow: 5px 5px #d7ccc8;
    border-radius: 8px;
    padding: 24px;
    margin-right: 24px;
    width: 25%;
}

